.container {
  width: 70%;
  margin: 15rem auto;
}

div h2 {
  color: white;
  font-size: 8rem;
  margin-bottom: 8rem;
  transition: all 0.2s;
}

div h2:hover {
  transform: scale(1.1);
  text-shadow: 0 1.5rem 2rem rgba(155, 1, 1, 0.8);
}

@media only screen and (max-width: 1000px) {
  .container {
    width: 90%;
  }
}
